@import url(https://fonts.googleapis.com/css?family=Roboto:500);

.App {
    display: block;
    text-align: center;
    padding: 0 5%;
    background-color: rgb(47, 83, 115);
    min-height: 180px;
    width: 85%;
    margin-left: 2%;
    border: 0.2vw solid rgb(185, 185, 185);
    box-shadow: inset 0 0 3px 0.2vw black;
}
.App * {
  box-sizing: border-box;
} 
h1{
    color: darkseagreen;
    border-top: 2px  darkseagreen solid;
    border-bottom: 2px darkseagreen solid;
}
.subtitle{
    color: antiquewhite;
    
}
.infoAlert {    
    width: 100%; 
    padding:  5px;
    height: 40px;
} 
.errorAlert {   
    width: 100%;
    padding: 0 5px;
    height: 40px;
} 
ul {
  padding: 0;
  list-style: none;
}
p{
    padding:0;
    margin-block-start: 0.1em;
    margin-block-end: 0.2em;
}
h4{
    padding-top: 5px;
    font-size: large;
}
.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}
input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}
.city {
  width: 250px;
}
.CitySearch {
 
  max-width: 300px;
  margin: 20px auto 0;
  position: relative;
}
.numberOfEvents {   
    margin: 20px auto;
    text-align: center;
    color: white;
}
.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 36px;
  font-size: 14px;
  box-shadow: 10px 10px 5px -5px rgba(23, 23, 23, 0.75);
}
.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}
.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.suggestions > li:hover {
  background-color: rgb(245, 251, 255);
  border: 1px rgb(32, 62, 89) solid;
}
.EventList{ 
    margin-top: 0px;
}
.event {
  padding: 0 2vw 3vw;
  text-align: justify;
  position: relative;
  margin-top: 0px;
  background-color: rgb(248, 248, 248);
  border-left: 0.3vw solid rgb(185, 185, 185);
  border-right: 0.3vw solid rgb(185, 185, 185);
  border-top: 0.15vw solid rgb(185, 185, 185);
  border-bottom: 0.15vw solid rgb(185, 185, 185);
  box-shadow: inset 0 0 0px 0.2vw rgba(47, 83, 115, 0.841);
}
.event:hover{ 
    background-color: rgb(255, 255, 255); 
}
.event .title{
    display: block;
    margin: 20px auto;
    letter-spacing: 0.1vw;
    text-align:center;
}
.event .title:hover{
    color:darkslategrey; 
    letter-spacing: 0.3vw;   
}
.fb-date-location{
    display: flex;
    align-items: flex-start;
    width: 100%;
    padding: 0.4vw;    
}
.start-time{
    flex-grow: 1;
    margin: 3% 6%;
    color: rgb(47, 83, 115);
    font-weight: 500;
    font-size: larger;
    padding: 1vw;  
    text-align: center;
    border: 3px white solid;
    box-shadow: 0 0 5px 5px rgb(47, 83, 115);
    border-radius: 3vw; 
    transform: rotate(-3deg);  
}
.start-time:hover{
    box-shadow: 0 0 8px 5px rgb(47, 83, 115);  
}
.location{
    flex-grow: 1;
    margin:  3.5% 6% 3%;
    text-align: center;
    padding: 1.2vw 0 ;
    border-top: 1px solid  rgb(47, 83, 115);
    border-bottom: 2px solid  rgb(47, 83, 115); 
    transform: rotate(2deg);   
}
.event-details{
    padding: 3vw 6vw 1vw;
}
.btn-toggle-details {
  display: block;
  width:fit-content;
  font-size: 10px;
  font-weight: 600;
  margin: 10px auto 0;
  padding: 10px 8px;
  border: 1px rgb(32, 62, 89) solid;
  border-radius: 4px;
  cursor: pointer;
  background-color: darkseagreen;
  color: rgb(47, 83, 115);
}
.btn-toggle-details:hover{
    background-color: rgb(47, 83, 115); 
    color: darkseagreen;
    border: 1px  darkseagreen solid;
}
.name {
  font-size: 18px; 
  font-weight: 600;
}
.group-name {
  text-transform: uppercase;
  font-size: 14px;
}
.going,
.visibility {
  font-style: italic;
}
.description {
  overflow-x: hidden;
}
.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}
.data-vis-wrapper {
  display: flex;
  align-items: center;
  background-color: darkseagreen;
  flex-direction: column;
}
.recharts-surface {
  overflow: visible;
}
.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}
.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  margin: 11px 11px 0 0;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
}
@media only screen and (min-width: 792px) {
  
  .App {
    padding: 0 5%;
    background-color: rgb(47, 83, 115);
    width: 50%;
    margin-left: 20%;
    }
    .Alert{
        width: 50%;
    }
}
@media all and (max-width: 450px) {
    .event-details{
        padding: 3vw 2vw 1vw;
        text-align: left;
    }

}